import React, { useState, useEffect } from 'react';
import { View, Panel, PanelHeader, Group, Cell, Avatar, Div, Button, List } from '@vkontakte/vkui';

const Main = ({ id, popout, view_go, fetchedUser, addRec, delRec, poolRecords, records }) => {
    const [activePanel, setActivePanel] = useState('main');
    
    useEffect(() => {
      
    }, [records.length]);
    
    const go = e => {
        setActivePanel(e.currentTarget.dataset.to);
	};
    
    let secs = []
    for (let sec in poolRecords) {
        secs.push(sec);  
    }

    return (
    <View id={id} activePanel={activePanel} popout={popout}>
	  <Panel id="main">
		<PanelHeader>Бассейн</PanelHeader>
		{fetchedUser &&
		<Group title="Добро пожаловать!">
			<Cell
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</Cell>
		</Group>}
        
        {records.length > 0 &&
        <Group title="Ваши ближайшие записи">
          <List>
            {records.map((sec) => {
              return (
              <Cell expandable onClick={delRec} data-sec={sec}>
                {`${poolRecords[sec].time}`}
              </Cell>);
            })}
          </List>
        </Group>
        }
        
        {secs.length > 0 &&
        <Group title="Открытые записи">
          <List>
            {secs.map((sec) => {
              if (records.indexOf(sec) === -1 && poolRecords[sec].count > 0)
              return(
              <Cell expandable onClick={addRec} data-sec={sec}>
                {`${poolRecords[sec].count} ${poolRecords[sec].time}`}
              </Cell>);
              return undefined;
            })}
          </List>
        </Group>
        }

		<Group title="Действия">
          <Div>
            <Button size="xl" level="2" onClick={view_go} data-to="mainUS">
              Выход
            </Button>
          </Div>
		</Group>
	  </Panel>
    </View>
    );
}

export default Main;