import React, { useState } from 'react';
import { View, Panel, PanelHeader, Group, Div, Button } from '@vkontakte/vkui';

const Admin = ({ id, popout, view_go, fetchedUser }) => {
    const [activePanel, setActivePanel] = useState('main');
    
    const go = e => {
        setActivePanel(e.currentTarget.dataset.to);
	};

    return (
    <View id={id} activePanel={activePanel} popout={popout}>
    
	  <Panel id="main">
		<PanelHeader>Админ-панель</PanelHeader>
		<Group title="Действия">
			<Div>
				<Button size="xl" level="2" onClick={go} data-to="main">
					Просмотр сеансов
				</Button>
                <Button size="xl" level="2" onClick={go} data-to="main">
					Добавить сеанс
				</Button>
                <Button size="xl" level="2" onClick={go} data-to="main">
					Удалить сеанс
				</Button>
                <Button size="xl" level="2" onClick={view_go} data-to="mainUS">
					Выход
				</Button>
			</Div>
		</Group>
	  </Panel>
      
    </View>
    );
}

export default Admin;