import React, { useState } from 'react';
import { View, Panel, PanelHeader, Group, Div, Button, HeaderButton, Cell } from '@vkontakte/vkui';
import { platform, IOS } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osName = platform();

const MainUS = ({ id, popout, view_go, fetchedUser }) => {
    const [activePanel, setActivePanel] = useState('main');
    
    const go = e => {
        setActivePanel(e.currentTarget.dataset.to);
	};

    return (
    <View id={id} activePanel={activePanel} popout={popout}>
    
	  <Panel id="main">
		<PanelHeader>Бассейн</PanelHeader>
		<Group title="Вход">
			<Div>
				<Button size="xl" level="2" onClick={view_go} data-to="main">
					Регистрация
				</Button>
                <Button size="xl" level="2" onClick={view_go} data-to="admin">
					Админ-панель
				</Button>
                <Button size="xl" level="2" onClick={go} data-to="info">
					Информация
				</Button>
			</Div>
		</Group>
	  </Panel>
      
      <Panel id="info">
		<PanelHeader
			left={<HeaderButton onClick={go} data-to="main">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
            Информация
        </PanelHeader>
        <Cell>
            Данное приложение предназначено для записи в бассейн.
		</Cell>
      </Panel>
      
    </View>
    );
}

export default MainUS;