import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import { Root, ScreenSpinner } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import ReactDOM from 'react-dom';

import MainUS from './views/MainUS';
import Main from './views/Main';
import Admin from './views/Admin';

let poolRecords = {};

console.log('App');

;(function () {
  
  let base_data = [
      [2020, 2, 25, 15, 0, 24],
      [2020, 2, 25, 19, 0, 2],
      [2020, 2, 29, 15, 0, 13],
      [2020, 2, 29, 19, 0, 4],
      [2020, 3, 1, 15, 0, 4],
      [2020, 3, 2, 19, 0, 2],
      [2020, 3, 3, 15, 0, 3],
      [2020, 3, 4, 19, 0, 1],
      [2020, 3, 5, 15, 0, 2],
      [2020, 3, 6, 15, 0, 1],
      [2020, 3, 6, 19, 0, 1]
  ];
    
  const make_date = el => {
      let t = new Date(el[0], el[1] - 1, el[2], el[3], el[4]);
      poolRecords[t.getTime()] = {time: t, count: el[5]};
      };
  base_data.map(make_date);
})()

const App = () => {
    const [activeView, setActiveView] = useState('mainUS');
    const [fetchedUser, setUser] = useState(null);
    const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
    const [records, setRecords] = useState([]);
    
    //const fetchedUser = {id: 154785330, first_name: "Максим", last_name: "Иванов" };
    
    useEffect(() => {
      async function fetchData() {
	    const user = await connect.send('VKWebAppGetUserInfo');
		setUser(user);
		setPopout(null);
        console.log('fetchData completed');
	  }
      fetchData();
    }, [/*count*/]); //Запускается только если изменилось значение переменной count*/
    
    const go = e => {
        setActiveView(e.currentTarget.dataset.to);
	};
    
    const addRec = e => {
        let sec = e.currentTarget.dataset.sec
        setPopout(<ScreenSpinner size='large' />);
        poolRecords[sec].count -= 1;
        setRecords(records.push(sec));
        setRecords(records.sort());
        ReactDOM.render(<App />, document.getElementById('root'));
        setPopout(null);
    };
    
    const delRec = e => {
        let sec = e.currentTarget.dataset.sec
        setPopout(<ScreenSpinner size='large' />);
        poolRecords[sec].count += 1;
        records.pop(records.indexOf(sec));
        ReactDOM.render(<App />, document.getElementById('root'));
        setPopout(null);
    };
    
    return (
      <Root activeView={activeView}>
        <MainUS id="mainUS" popout={popout} view_go={go} fetchedUser={fetchedUser}/>
        <Main id="main" popout={popout} view_go={go} fetchedUser={fetchedUser} addRec={addRec} delRec={delRec} poolRecords={poolRecords} records={records}/>
        <Admin id="admin" popout={popout} view_go={go} fetchedUser={fetchedUser}/>
      </Root>
    );
}

export default App;

